@import "../base/mixins.scss";
@import "../base/vars.scss";

.card__link {
  display: block;

  @media (min-width: $width_tablet) {
    flex-basis: 50%;
  }
}

.card__img {
  width: 100%;
}

.card__wrapper-feature {
  padding: 25px;

  @media (min-width: $width_tablet) {
    flex-basis: 50%;
    display: flex;
    flex-direction: column;
  }
}

.card__title {
  margin: 0;
  color: $color_black;

  @media (min-width: $width_tablet) {
    padding: 0 0 15px;
  }
}

.card__caption {
  margin: 0;
  font-weight: $font_weight_normal;
  color: $color_greyish_brown;
}

.card__wrapper-price {
  padding: 30px 0 0;
  margin: auto 0 0;
  display: flex;
  justify-content: space-between;
}

.card__price {
  margin: 0;
}

.card__button {
  width: 30px;
  height: 30px;
  border: none;
  opacity: 0.2;
}
