@import "../base/mixins.scss";
@import "../base/vars.scss";

.footer__inner {
  @include inner;

  background: $color_gray_lightest;

  @media (min-width: $width_tablet) {
    padding: $padding_medium;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.footer__logo {
  display: none;

  @media (min-width: $width_tablet) {
    display: block;
  }
}

.footer__list {
  @include list;

  display: flex;
  justify-content: space-around;
}

.footer__social {
  padding: 25px 60px;

  @media (min-width: $width_tablet) {
    padding: 60px 0;
    min-width: 200px;
  }
}

.footer__copyright {
  padding: 25px 40px;
  display: flex;
  justify-content: space-around;

  @media (min-width: $width_tablet) {
    padding: 60px 0;
  }
}

.footer__text {
  margin: 0;
  padding: 0 50px 0 0;
}

.footer__icon {
  & path {
    fill: $color_seafoam_blue;
  }
}
