@mixin inner() {
  width: 100%;
  margin: 0 auto;
  padding: $padding_small;
}

@mixin fixed_inner() {
  width: 100%;
  padding: $padding_small;
}

@mixin list() {
  margin: 0;
  padding: 0;

  list-style: none;
}

@mixin btn() {
  display: block;
  width: 260px;
  min-height: 52px;
  padding: 14px 0;
  margin: 0 auto;

  text-align: center;
  font-size: $font_size_h3_mobile;
  line-height: $line_height_h3_mobile;
  font-weight: $font_weight_bold;
  text-transform: uppercase;
  color: $color_greyish_brown;

  border: 2px solid $color_greyish_brown;
  background: $color_white;
}
