@import "../base/mixins.scss";
@import "../base/vars.scss";

.btn {
  @include btn;

  &--order {
    width: 192px;

    @media (min-width: $width_tablet) {
      width: 100%;
      padding: 18px 0;
    }

    @media (min-width: $width_desktop) {
      width: 230px;
      margin: 0;
    }
  }

  &--buy {
    @media (min-width: $width_tablet) {
      width: 292px;
      padding: 18px 0;
      margin: 0;
    }
  }

  &--write {
    @media (min-width: $width_tablet) {
      margin: 20px 0 45px;
      padding: 18px 0;
      width: 175px;
      order: 2;
    }
  }

  &--сontact {
    @media (min-width: $width_tablet) {
      width: 230px;
      padding: 18px 0;
    }

    @media (min-width: $width_desktop) {
      margin: 0;
    }
  }

  &--form {
    margin: 0;

    @media (min-width: $width_tablet) {
      width: 230px;
    }

    @media (min-width: $width_desktop) {
      margin: 0 auto 0 0;
    }
  }
}
